.edit-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.499);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1; 
  }
  
.edit-modal {
    background-color: rgba(255, 255, 255, 0.946);
    padding: 20px;
    border-radius: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    z-index: 1; 
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 40%;
}

.edit-task-title {
    font-size: 32px;
    color: #20948B;
    margin: 20px;
  }
  
.edit-title-input {
    width: 85%;
    margin: 10px;
    height: 38px;
    border-radius: 20px;
    font-size: 20px;
    border: 1px solid rgba(0, 0, 0, 0.35);
    padding-left: 12px;
    padding-right: 12px;
}
  
.edit-description-input {
    width: 85%;
    margin: 10px;
    height: 58px;
    border-radius: 20px;
    font-size: 18px;
    padding-left: 12px;
    padding-right: 12px;
    border: 1px solid rgba(0, 0, 0, 0.35);
}
  
.edit-date-input {
    width: 85%;
    margin: 10px;
    height: 38px;
    border-radius: 20px;
    font-size: 18px;
    padding-left: 12px;
    padding-right: 12px;
    border: 1px solid rgba(0, 0, 0, 0.35);
}
  
.edit-checkbox-important {
    font-size: 21px;
    font-weight: bold;
    color: rgba(0, 0, 0, 0.583);
}
  
  .button-container {
    display: flex;
    justify-content: center;
    margin: 20px;
  }
  
.edit-save-button {
    border:1px solid #DE7A22;
    color: #DE7A22; 
    border-radius: 20px; 
    cursor: pointer;
    font-size: 16px;
    font-weight: bold;
    width: 150px;
    height: 40px;
    margin-left: 12px;
    margin-right: 12px;
    box-shadow: 0px 0px 3px gray;
}
  
.edit-save-button:hover {
    border:1px solid rgba(128, 128, 128, 0.55);
    background-color: #DE7A22;
    color: white;
}
  
.edit-cancel-button {
    border:1px solid grey;
    color: grey; 
    border-radius: 20px; 
    cursor: pointer;
    font-size: 16px;
    font-weight: bold;
    width: 150px;
    height: 40px;
    margin-left: 12px;
    margin-right: 12px;
    box-shadow: 0px 0px 3px gray;
}
  
.edit-cancel-button:hover {
  border:1px solid rgba(128, 128, 128, 0.55);
  background-color: grey;
  color: white;
}
  